<template>
  <div>
    <el-table-block :column="column" :data="rows" :merge="merge" border />
    <!-- <el-table-block :column="column" :data="rows" :span-method="spanMethod" border> </el-table-block> -->
  </div>
</template>

<script>
export default {
  data() {
    return {
      searchValue: '',
      column: [
        {
          type: 'index',
          renderBody(h, data) {
            return <b>{data.$index + 1}</b>
          }
        },
        {
          prop: 'mpName',
          label: ''
        },
        {
          prop: 'processTypeName',
          label: ''
        },
        {
          prop: 'equipTypeName',
          label: ''
        },
        {
          prop: 'equipTypeId',
          label: '',
          formatter(data) {
            return data.equipTypeId + ''
          }
        },
        {
          prop: 'equipId',
          label: ''
        },
        {
          prop: 'processTypeCode',
          label: '',
          renderHeader(h) {
            return (
              <el-input
                value={this.searchValue}
                onInput={(value) => (this.searchValue = value)}
                size="mini"
                placeholder=""
              />
            )
          },
          renderBody(h) {
            return (
              <el-button
                type="primary"
                size="mini"
                onClick={() => {
                  alert(1)
                }}
              >
                
              </el-button>
            )
          }
        }
      ],
      rows: [],
      merge: ['mpName', 'equipTypeId']
    }
  },
  mounted() {
    this.fetchTable()
  },
  methods: {
    setTable(tableData, merge) {
      this.mergeLine = {}
      this.mergeIndex = {}
      // eslint-disable-next-line
      merge.forEach(item => {
        tableData.forEach((data, i) => {
          if (i === 0) {
            this.mergeIndex[item] = this.mergeIndex[item] || []
            this.mergeIndex[item].push(1) // this.mergeIndex['mpName'] = [1]

            this.mergeLine[item] = 0 // this.mergeLine['mpName'] = 0
          } else {
            if (data[item] === tableData[i - 1][item]) {
              // 
              this.mergeIndex[item][this.mergeLine[item]] += 1
              // 
              this.mergeIndex[item].push(0)
            } else {
              // 
              this.mergeIndex[item].push(1)
              // console.log('i', i)
              this.mergeLine[item] = i
            }
          }
        })
      })
    },
    spanMethod({ column, rowIndex }) {
      const index = this.merge.indexOf(column.property)
      if (index > -1) {
        const _row = this.mergeIndex[column.property][rowIndex]
        const _col = _row > 0 ? 1 : 0 // 1  0
        return {
          rowspan: _row,
          colspan: _col
        }
      }
    },
    async fetchTable() {
      // const { data } = await fetch('http://rap2.taobao.org:38080/app/mock/data/1679024').then((res) => res.json())
      const data = [
        {
          mpId: '89283938294',
          mpName: '',
          processTypeCode: '1',
          processTypeName: '',
          equipType: [
            {
              equipTypeId: '1',
              equipTypeName: ''
            },
            {
              equipTypeId: '2',
              equipTypeName: ''
            },
            {
              equipTypeId: '3',
              equipTypeName: ''
            }
          ],
          equipId: '1220218'
        },
        // {
        //   mpId: '89283938293',
        //   mpName: '',
        //   processTypeCode: '1',
        //   processTypeName: '',
        //   equipType: [
        //     {
        //       equipTypeId: '1',
        //       equipTypeName: ''
        //     },
        //     {
        //       equipTypeId: '2',
        //       equipTypeName: ''
        //     },
        //     {
        //       equipTypeId: '3',
        //       equipTypeName: ''
        //     }
        //   ],
        //   equipId: '111'
        // }
        {
          mpId: '89283938294',
          mpName: '',
          processTypeCode: '2',
          processTypeName: '',
          equipType: [
            {
              equipTypeId: '2',
              equipTypeName: ''
            },
            {
              equipTypeId: '2',
              equipTypeName: ''
            }
          ],
          equipId: '1000212'
        }
        // {
        // mpId: '434342323',
        // mpName: '',
        // processTypeCode: '11',
        // processTypeName: '',
        // equipType: [
        //   {
        //     equipTypeId: '1',
        //     equipTypeName: ''
        //   },
        //   {
        //     equipTypeId: '2',
        //     equipTypeName: ''
        //   },
        //   {
        //     equipTypeId: '3',
        //     equipTypeName: ''
        //   }
        // ],
        // equipId: '3333'
        // }
      ]
      this.rows = data.reduce((acc, v) => {
        // eslint-disable-next-line
        v.equipType.forEach(vv => {
          vv.rows = v.equipType.length
          vv.equipId = v.equipId
          vv.mpName = v.mpName
          vv.processTypeName = v.processTypeName
        })
        acc.push(...v.equipType)
        return acc
      }, [])
      this.setTable(this.rows, this.merge)
    }
  }
}
</script>

<style>
.el-table .warning-row {
  background: oldlace;
}

.el-table .success-row {
  background: #f0f9eb;
}
</style>
